<template>
  <div class="box" v-loading="loading" element-loading-text="拼命加载中">
    <div class="handle-box">
      <div class="btn">
        <el-button slot="append" icon="el-icon-refresh" @click="refresh"></el-button>
      </div>

      <div class="btn" v-if="username == 'admin'">
        <span>学校：</span>
        <el-select v-model="school" placeholder="请选择学校" @change="changeSchoolType">
          <el-option v-for="item in schoolList" :key="item.index" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </div>
      <div class="btn" v-if="username == 'admin'">
        <span>年级：</span>
        <el-select v-model="grade" placeholder="请选择年级" @change="changeGradeType">
          <el-option v-for="item in gradeList" :key="item.index" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </div>
      <div class="btn" v-if="username !== 'admin'">
        <span>年级：</span>
        <el-select v-model="grade" placeholder="请选择年级" @change="changeGradeType">
          <el-option v-for="item in gradeList" :key="item.index" :label="item.title" :value="item.id"></el-option>
        </el-select>
      </div>
    </div>

    <div id="bar-chart"></div>
    <div class="pie-box">
      <div id="pie-chart-user"></div>
      <div id="pie-chart-student"></div>
    </div>
  </div>
</template>

<script>
  import API from "@/api/index.js";

  require("echarts");

  export default {
    name: "studentsTatal",
    inject: ["reload"],

    data() {
      return {
        isAdmin: true,
        school: "",
        grade: "",
        loading: true,
        schoolList: [],
        gradeList: [],
        nameList: [],
        countList: [],
        existCount: [],

        school_id: "",
        grade_id: "",
        bar: {
          title: {
            text: "学生统计",
            left: "center",
          },
          tooltip: {},
          legend: {
            orient: "vertical",
            data: ["总学生数", "已注册人数"],
            left: "right",
          },
          xAxis: {
            data: [],
          },
          yAxis: {},
          series: [],
          dataZoom: [{
            type: "inside",
          }, ],
        },
        pie: {
          title: {},
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b} : {c} ({d}%)",
          },
          legend: {
            orient: "vertical",
            left: "right",
            data: ["男生人数", "女生人数"],
          },
          series: [{
            name: "",
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            label: {
              normal: {
                formatter: "{d}%",
                position: "inside",
              },
            },
            data: [{
                value: 0,
                name: "男生人数"
              },
              {
                value: 0,
                name: "女生人数"
              },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          }, ],
        },
        title: {
          user: "用户男女比例",
          student: "学生男女比例",
        },
        username: localStorage.getItem("username"),
      };
    },

    methods: {
      // 刷新
      refresh() {
        this.reload();
      },
      // 饼图封装
      fucBarUser(sex, type, school_id, grade_id) {
        var self = this;
        let pieUserChart = self.$echarts.init(document.getElementById('pie-chart-user'));
        API.studentGender(sex, type, school_id, grade_id).then((res) => {
          pieUserChart.setOption(
            (self.pie = {
              title: {
                text: self.title.user,
                left: "center",
              },
              series: [{
                name: self.title.user,

                data: [{
                    value: res.man_total,
                    name: "男生人数"
                  },
                  {
                    value: res.woman_total,
                    name: "女生人数"
                  },
                ],
              }],
            })
          );
          self.loading = false;
        });
      },
      fucBarStu(sex, type, school_id, grade_id) {
        var self = this;
        let pieStudentChart = self.$echarts.init(document.getElementById('pie-chart-student'));
        API.studentGender(sex, type, school_id, grade_id).then((res) => {
          pieStudentChart.setOption(
            (self.pie = {
              title: {
                text: self.title.student,
                left: "center",
              },
              series: [{
                name: self.title.student,

                data: [{
                    value: res.man_total,
                    name: "男生人数"
                  },
                  {
                    value: res.woman_total,
                    name: "女生人数"
                  },
                ],
              }, ],
            })
          );
          self.loading = false;
        });
      },
      changeSchoolType(value) {
        var self = this;
        self.loading = true;
        let barChart = self.$echarts.init(document.getElementById('bar-chart'));
        self.nameList = [];
        self.countList = [];
        self.existCount = [];
        self.grade = "";
        self.school_id = value;
        API.statistics(self.school_id).then((res) => {
          self.gradeList = res.data;
          res.data.forEach((item) => {
            self.nameList.push(item.name);
            self.countList.push(item.count);
            self.existCount.push(item.exist_count);
          });
          var zoomSize = 6;
          barChart.on("click", function (params) {
            barChart.dispatchAction({
              type: "dataZoom",
              startValue: self.nameList[Math.max(params.dataIndex - zoomSize / 2, 0)],
              endValue: self.nameList[
                Math.min(
                  params.dataIndex + zoomSize / 2,
                  self.nameList.length - 1
                )
              ],
            });
          });
          barChart.setOption(
            (self.bar = {
              xAxis: {
                data: self.nameList,
              },
              series: [{
                  name: "总学生数",
                  type: "bar",
                  label: {
                    show: true,
                    position: "inside",
                  },
                  data: self.countList,
                },
                {
                  name: "已注册人数",
                  type: "bar",
                  label: {
                    show: true,
                    position: "inside",
                  },
                  data: self.existCount,
                },
              ],
            })
          );
          self.fucBarUser(0, 1, self.school_id);
          self.fucBarStu(0, 2, self.school_id);
          self.loading = false;
        });
      },
      changeGradeType(value) {
        var self = this;
        self.loading = true;
        let barChart = self.$echarts.init(document.getElementById('bar-chart'));
        self.grade_id = value;
        self.nameList = [];
        self.countList = [];
        self.existCount = [];
        var zoomSize = 6;
        barChart.on("click", function (params) {
          console.log(
            self.nameList[Math.max(params.dataIndex - zoomSize / 2, 0)]
          );
          barChart.dispatchAction({
            type: "dataZoom",
            startValue: self.nameList[Math.max(params.dataIndex - zoomSize / 2, 0)],
            endValue: self.nameList[
              Math.min(
                params.dataIndex + zoomSize / 2,
                self.nameList.length - 1
              )
            ],
          });
        });
        if (self.username == "admin") {
          API.statistics(self.school_id, self.grade_id).then((res) => {
            res.data.forEach((item) => {
              self.nameList.push(item.name);
              self.countList.push(item.count);
              self.existCount.push(item.exist_count);
            });
            barChart.setOption(
              (self.bar = {
                xAxis: {
                  data: self.nameList,
                },
                series: [{
                    name: "总学生数",
                    type: "bar",
                    label: {
                      show: true,
                      position: "inside",
                    },
                    data: self.countList,
                  },
                  {
                    name: "已注册人数",
                    type: "bar",
                    label: {
                      show: true,
                      position: "inside",
                    },
                    data: self.existCount,
                  },
                ],
              })
            );
            self.fucBarUser(0, 1, self.school_id, self.grade_id);
            self.fucBarStu(0, 2, self.school_id, self.grade_id);
            self.loading = false;
          });
        } else {
          API.statistics(0, self.grade_id).then((res) => {
            res.data.forEach((item) => {
              self.nameList.push(item.name);
              self.countList.push(item.count);
              self.existCount.push(item.exist_count);
            });
            barChart.setOption(
              (self.bar = {
                xAxis: {
                  data: self.nameList,
                },
                series: [{
                    name: "总学生数",
                    type: "bar",
                    label: {
                      show: true,
                      position: "inside",
                    },
                    data: self.countList,
                  },
                  {
                    name: "已注册人数",
                    type: "bar",
                    label: {
                      show: true,
                      position: "inside",
                    },
                    data: self.existCount,
                  },
                ],
              })
            );
            self.fucBarUser(0, 1, 0, self.grade_id);
            self.fucBarStu(0, 2, 0, self.grade_id);
            self.loading = false;
          });
        }
      },

      // 总后台
      getTotal() {
        var self = this;
        let barChart = self.$echarts.init(document.getElementById('bar-chart'));
        let pieUserChart = self.$echarts.init(document.getElementById('pie-chart-user'));
        let pieStudentChart = self.$echarts.init(document.getElementById('pie-chart-student'));
        barChart.setOption(self.bar);
        pieUserChart.setOption(self.pie);
        pieStudentChart.setOption(self.pie);
        self.nameList = [];
        self.countList = [];
        self.existCount = [];
        self.gradeList = [];
        self.school = "";
        self.grade = "";
        self.isAdmin = true;
        if (self.username == "admin") {
          API.schools(1, 1000).then((res) => {
            self.schoolList = res.data;
          });
        } else {
          API.grades().then((res) => {
            self.gradeList = res.data;
          });
        }
        API.statistics().then((res) => {
          res.data.forEach((item) => {
            self.nameList.push(item.name);
            self.countList.push(item.count);
            self.existCount.push(item.exist_count);
          });
          var zoomSize = 6;
          barChart.on("click", function (params) {
            barChart.dispatchAction({
              type: "dataZoom",
              startValue: self.nameList[Math.max(params.dataIndex - zoomSize / 2, 0)],
              endValue: self.nameList[
                Math.min(
                  params.dataIndex + zoomSize / 2,
                  self.nameList.length - 1
                )
              ],
            });
          });
          barChart.setOption(
            (self.bar = {
              xAxis: {
                data: self.nameList,
              },
              series: [{
                  name: "总学生数",
                  type: "bar",
                  label: {
                    show: true,
                    position: "inside",
                  },
                  data: self.countList,
                },
                {
                  name: "已注册人数",
                  type: "bar",
                  label: {
                    show: true,
                    position: "inside",
                  },
                  data: self.existCount,
                },
              ],
            })
          );
          self.loading = false;
        });
        self.fucBarUser(0, 1);
        self.fucBarStu(0, 2);
      },
    },

    mounted() {
      this.getTotal();
    },
  };
</script>

<style lang="scss" scoped>
  .box {
    overflow: hidden;
  }

  #bar-chart {
    width: 100%;
    height: 500px;
  }

  .pie-box #pie-chart-user {
    width: 50%;
    height: 500px;
    float: left;
  }

  .pie-box #pie-chart-student {
    width: 50%;
    height: 500px;
    float: left;
  }
</style>